.loader {
  animation: flix 5s ease-in infinite alternate;
  position: relative;
}

.loader:before {
  content: "";
  width: 48px;
  height: 48px;
  background-color: #fff;
  background-image: radial-gradient(12px at 22px 22px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(10px at 6px 40px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(14px at 31px -6px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px at 40px 30px, #ff3d00 100%, rgba(0, 0, 0, 0) 0);
  border-radius: 50%;
  font-size: 0;
  animation: rotate 1s linear infinite;
  display: block;
}

.loader:after {
  content: "";
  width: 24px;
  height: 12px;
  background: #fff;
  border-radius: 50px 50px 0 0;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -100%);
}

@keyframes flix {
  0%, 60% {
    transform: rotate(-10deg);
  }

  100%, 30%, 80% {
    transform: rotate(5deg);
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

